import {
	SET_ERRORS,
	CLEAR_ERRORS,
	SET_AUTHENTICATED,
	SET_USER,
	LOADING_UI,
	SET_ADMIN,
	STOP_LOADING_UI,
	SET_OPERATION_SUCCESS,
	LOGOUT,

} from '../type';
import axios from 'axios';

export const loginUser = (userData, history) => async (dispatch) => {
	dispatch({ type: CLEAR_ERRORS });
	dispatch({ type: LOADING_UI });
	axios
		.post('/api/authenticate', userData)
		.then((res) => {
			dispatch({ type: SET_AUTHENTICATED });
			setAuthorizationHeader(res.data.jwt, res.data.domain);
			dispatch(getUserData(res.data.domain, history));
			dispatch({ type: STOP_LOADING_UI });
			if (res.data.domain === 'STUDENT') {
				history.push('/student');
			} else {
				history.push('/admin');
			}
		})
		.catch((err) => {
			console.log(err);
			dispatch({
				type: SET_ERRORS,
				payload: err.response.data
			});
		});
};

export const getUserData = (domain) => (dispatch) => {
	let url;
	if (domain === 'STUDENT') {
		url = '/api/student/data';
		axios
			.get(url)
			.then((res) => {
				dispatch({
					type: SET_USER,
					payload: res.data
				});
			})
			.catch((err) => {
				console.log(err);
				dispatch({
					type: SET_ERRORS,
					payload: err
				});
			});
	} else {
		url = '/api/admin/data';
		axios.get(url).then((res) => {
			axios
				.get('/api/admin/adminDashbord')
				.then((dashboard) => {
					console.log(dashboard.data);
					dispatch({
						type: SET_ADMIN,
						payload: res.data,
						dashboard: dashboard.data
					});
				})
				.catch((err) => console.log(err));
		});
	}
};

export const RegisterStudent = (formData, history) => (dispatch) => {
	const config = {
		header: { 'content-type': 'multipart/form-data' }
	};
	axios
		.post('/api/registerStudent', formData, config)
		.then((res) => {
			history.push('/login');
		})
		.catch((err) => console.log(err));
};

export const updateStudent = (formData) => (dispatch) => {
	dispatch({ type: CLEAR_ERRORS });
	dispatch({ type: LOADING_UI });
	const config = {
		header: { 'content-type': 'multipart/form-data' }
	};
	axios
		.post('/api/student/updateStudent', formData, config)
		.then((res) => {
			dispatch(getUserData('STUDENT'));
			dispatch({ type: SET_OPERATION_SUCCESS, payload: 'success' });
		})
		.catch((err) => {
			console.log(err);
			dispatch({
				type: SET_ERRORS,
				payload: err.response.data
			});
		});
};

