export const changePhotoOrSign = (values, domain) => (dispatch) => {
	dispatch({ type: CLEAR_ERRORS });
	const formData = new FormData();
	let url;
	let key;
	if (values['sign']) {
		key = 'sign';
		formData.append(key, values[key], values[key].name);
		url = domain + '/changeSign';
	} else {
		key = 'photo';
		formData.append(key, values[key], values[key].name);
		url = domain + '/changePhoto';
	}

	const config = {
		header: { 'content-type': 'multipart/form-data' }
	};
	axios
		.post("/api"+url, formData, config)
		.then(async (res) => {
			if (domain === "/student") {
				dispatch(getUserData("STUDENT"))
			} else {
				dispatch(getUserData("ADMIN"))
			}

		})
		.catch((err) => {
			console.log(err);
			dispatch({
				type: SET_ERRORS,
				payload: err.response.data
			});
		});
};

export const changePassword = (formData, domain) => (dispatch) => {
	dispatch({ type: CLEAR_ERRORS });
	dispatch({ type: LOADING_UI });
	const config = {
		header: { 'content-type': 'multipart/form-data' }
	};
	axios
		.post(`/api${domain}/changePassword`, formData, config)
		.then((res) => {
			dispatch({ type: SET_OPERATION_SUCCESS, payload: 'success' });
		})
		.catch((err) => {
			console.error(err);
			dispatch({ type: SET_ERRORS, payload: err.response.data });
		});
};



export const requestCertificate = (type, formData, history) => (dispatch) => {
	dispatch({ type: CLEAR_ERRORS });
	dispatch({ type: LOADING_UI });

	const config = {
		header: { 'content-type': 'multipart/form-data' }
	};
	const link = type === "feerefund" ? '/api/student/feeRefund' : `/api/student/DocumentSubmit/${type}`;
	axios
		.post(link, formData, config)
		.then((res) => {
			dispatch({ type: SET_OPERATION_SUCCESS, payload: 'Success' });
			dispatch(getUserData("STUDENT"))
			dispatch({ type: CLEAR_ERRORS })
			history.push('/student')
		})
		.catch((err) => {
			console.error(err);
			dispatch({ type: SET_ERRORS, payload: err.response.data });
		});
};





