export const logout = () => (dispatch) => {
	dispatch({
		type: LOGOUT
	})
}

const setAuthorizationHeader = (token, domain) => {
	const FBIdToken = `Bearer ${token}`;
	axios.defaults.headers.common['Authorization'] = FBIdToken;
	axios.defaults.headers.common['Domain'] = domain;
};
